@import '../reset.css';
$color:#121211;

.logo {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: $color;
  color: #fff;
  font-size: 30px;
  font-weight: bold;
  padding: 30px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.container {
  background-color: $color;
  display: flex;
  padding-top: 90px;
  min-height: calc(100vh - 90px);
  .box {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    justify-content: space-around;
    flex-direction: row;
    margin: 0 auto;
    gap: 20px;
  }
  .loading {
    display: flex;
    width: 100%;
    height: calc(100vh - 90px);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 30px;
    color: white;
  }
}
