@import '../reset.css';
$color:#121211;
$uppercase:uppercase;
.logo {
  position: fixed;
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  align-items: center;
  background-color: $color;
  color: #fff;
  font-size: 30px;
  font-weight: bold;
  padding: 30px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
.container {
  min-height: calc(100vh - 90px);
  padding-top: 90px;
  background-color: $color;
  .loading {
    display: flex;
    width: 100%;
    height: calc(100vh - 90px);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 30px;
    color: white;
  }
  .container_box {
    width: 60%;
    display: flex;
    justify-content:space-around;
    margin: 0 auto;
    .text_box {
      width: 40%;
      padding: 20px;
      color: rgba(245, 245, 245, 0.911);
      .title {
        font-size: 30px;
        margin-bottom: 20px;
      }      
      .traiiler{
        width: 100%;
        height: 30vh;
        margin-top: 20px;
      }
      dt {
        font-weight: bold;
        font-size: 16px;
        margin-top: 10px;
      }
      dd {
        font-size: 14px;
        span {
          display: inline-block;
          margin-right: 5px;
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}